import { Helmet } from "react-helmet";

import Header from "../header/Header";
import Footer from "../footer/Footer";
import Button from "../button/Button";

import mainImg from "../../images/coffee-img.svg";

const Page404 = () => {
    return (
        <>
            <Helmet>
                <meta
                    name="description"
                    content="This page is not found"
                />
                <title>404 page</title>
            </Helmet>
            <Header/>
            <div className="page-holder">
                <main className="main" id="main">
                <section className="error-section">
                    <div className="container">
                        <div className="row">
                            <div className="img-block">
                                <img src={mainImg} alt="Man with cup of coffee" />
                            </div>
                            <div className="text-block">
                                <h1>404 Page not found</h1>
                                <p>Sorry, we canât find page you are looking for.</p>
                                <Button useClass="btn" url="/" text="Back home"></Button>
                            </div>
                        </div>
                    </div>
                </section>
                </main>
            </div>
            <Footer/>
        </>
    )
}

export default Page404;